/* Home.css */

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.home-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  height: auto; /* Full height of the viewport */
  background-color: white;
}

.home-content {
  padding: 20px; /* Add padding for better spacing */
  text-align: center;
}
.home-text{
  color:black;
}
.home-content h3 {
  color: black;
  padding-top: 10px;
  margin-left: 10px;
}

.home-content h2 {
  padding-top: 5px;
  margin-left: 10px;
  color: #2bc179;
}

.home-button {
  margin-left: 10px;
  padding-top: 20px;
}

.home-button .btn-hire {
  background-color: rgb(11, 11, 237);
  color: black;
  width: 150px;
}

.home-button .btn-hire:hover {
  background-color: white;
  color: rgb(11, 11, 237);
  width: 160px;
  transition: 1s;
}

.home-button .btn-cv {
  background-color: rgb(237, 11, 11);
  color: black;
  margin-left: 15px;
}

.home-button .btn-cv:hover {
  opacity: 80%;
  background-color: black;
  color: rgb(11, 11, 237);
  transition: 1s;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  .home-content {
    padding: 5px; /* Adjust padding for smaller screens */
  }

  .home-content h3,
  .home-content h2 {
    margin-left: 0; /* Reset margin for smaller screens */
  }

  .home-button {
    margin-top: 10px; /* Adjust margin for smaller screens */
    margin-left: 0; /* Reset margin for smaller screens */
    text-align: center; /* Center align buttons */
  }

  .home-button .btn-hire,
  .home-button .btn-cv {
    margin-right: 0; /* Reset margin for smaller screens */
    margin-bottom: 10px; /* Add margin between buttons for smaller screens */
    display: block; /* Make buttons full-width on smaller screens */
  }
}
#dark .container-fluid{
  background-color: black;
}
#dark h1{
  color:aliceblue;
}